import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';
import Card from '@mui/material/Card';
import CardMedia from '@mui/material/CardMedia';
import Chip from '@mui/material/Chip';
import ReactMarkdown from 'react-markdown';
import remarkGfm from 'remark-gfm';

// Helper to parse frontmatter
function extractFrontmatter(md) {
  const match = md.match(/^---([\s\S]*?)---/);
  if (!match) return {};
  const lines = match[1].split('\n');
  const obj = {};
  lines.forEach(line => {
    const [key, ...rest] = line.split(':');
    if (key && rest.length) obj[key.trim()] = rest.join(':').trim();
  });
  return obj;
}

function getBody(md) {
  return md.replace(/^---([\s\S]*?)---/, '').trim();
}

const writingsContext = require.context('./writings', false, /^(?!.*TEMPLATE).*\.md$/);

const Writing = () => {
  const { slug } = useParams();
  const [post, setPost] = useState(null);

  useEffect(() => {
    const files = writingsContext.keys();
    const file = files.find(f => {
      const md = writingsContext(f).default || writingsContext(f);
      const front = extractFrontmatter(md);
      return (front.slug || f.replace('./', '').replace('.md', '')) === slug;
    });
    if (file) {
      const md = writingsContext(file).default || writingsContext(file);
      const front = extractFrontmatter(md);
      setPost({
        ...front,
        body: getBody(md),
      });
    }
  }, [slug]);

  if (!post) return <Typography>Loading...</Typography>;

  const coverImg = post.cover
    ? require(`./writings/${post.cover.replace('./', '')}`)
    : require('./writings/images/binary-cover.png');

  return (
    <Box maxWidth={800} mx="auto" mt={4}>
      <Card>
        <CardMedia
          component="img"
          height="300"
          image={coverImg}
          alt={post.Title || post.title}
          sx={{ objectFit: 'cover' }}
        />
      </Card>

      <Typography variant="h3" mt={2} gutterBottom>
        {post.Title || post.title}
      </Typography>

      <Typography variant="subtitle1" color="textSecondary" gutterBottom>
        {post.date} {post.readingTime && `• ${post.readingTime} min read`}
      </Typography>

      <Box mb={2}>
        {post.tags &&
          post.tags.replace(/\[|\]|'/g, '').split(',').map(tag => (
            <Chip key={tag.trim()} label={tag.trim()} sx={{ mr: 1 }} />
          ))}
      </Box>

      {/* Render Markdown with styles */}
      <Box sx={{ typography: 'body1' }}>
        <ReactMarkdown
          children={post.body}
          remarkPlugins={[remarkGfm]}
          components={{
            h1: ({node, ...props}) => <Typography variant="h4" gutterBottom {...props} />,
            h2: ({node, ...props}) => <Typography variant="h5" gutterBottom {...props} />,
            h3: ({node, ...props}) => <Typography variant="h6" gutterBottom {...props} />,
            p: ({node, ...props}) => <Typography paragraph {...props} />,
            code: ({node, inline, className, children, ...props}) => (
              <Box
                component="pre"
                sx={{
                  backgroundColor: '#f5f5f5',
                  p: 1,
                  borderRadius: 1,
                  overflowX: 'auto',
                  fontSize: '0.9rem',
                }}
              >
                <code className={className} {...props}>{children}</code>
              </Box>
            ),
          }}
        />
      </Box>
    </Box>
  );
};

export default Writing;
